* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin: 0;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.search-form {
  background-color: turquoise;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
}

input {
  padding-top: 8px;
  padding-bottom: 8px;
}

.submit-button {
  border-radius: 4px;
  padding: 8px 32px;
}

.submit-button:hover,
.submit-button:focus {
  cursor: pointer;
  background-color: ;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.photo-card {
  width: 300px;
  margin-bottom: 20px;
  box-shadow: 0px 1px 6px rgba(46, 47, 66, 0.08),
    0px 1px 1px rgba(46, 47, 66, 0.16), 0px 2px 1px rgba(46, 47, 66, 0.08);
  border-radius: 0px 0px 4px 4px;
}

.info {
  width: 300px;
  display: flex;
  text-align: center;
}
